
import { defineComponent, onMounted, ref } from "vue";
import { gsap } from "gsap";
export default defineComponent({
  name: "HeaderMenu",
  setup() {
    const activedSection = ref("");

    onMounted(() => {
      gsap.set(".list", {
        opacity: 0,
      });
      gsap.to(".list", {
        duration: 2,
        delay: 1,
        opacity: 1,
      });

      gsap.set(".header__img", {
        opacity: 0,
      });
      gsap.to(".header__img", {
        duration: 2,
        delay: 1,
        opacity: 1,
      });
    });
    return {
      activedSection,
    };
  },
});
